/* Import the variable font */
@font-face {
    font-family: 'Outfit';
    src: url('./fonts/Outfit-VariableFont_wght.ttf') format('truetype');
    font-weight: 100 900;
    font-style: normal;
  }
  
  /* Import each static font style */
  @font-face {
    font-family: 'Outfit Thin';
    src: url('./fonts/static/Outfit-Thin.ttf') format('truetype');
    font-weight: 100;
    font-style: normal;
  }
  
  @font-face {
